@charset "UTF-8";
.handsontable {
  --hot-foreground-color: #373737;
  --hot-background-color: #ffffff;
  --hot-border-color: #cccccc;
  --hot-header-background-color: #f0f0f0;
  --hot-header-foreground-color: #222222;
  --hot-header-active-background-color: var(--hot-border-color);
  --hot-link-accent-color: #104acc;
  --hot-invalid-background-color: #ffbeba;
  --hot-mobile-focus-color: #5292f7;
  --hot-column-resizer-color: #34a9db;
  --hot-column-resizer-guide-color: #777777;
  --hot-link-color: var(--hot-link-accent-color);
  --hot-border-horizontal-size: 1px;
  --hot-border-vertical-size: 1px;
  --hot-base-padding: 4px;
  --hot-base-font-size: 13px;
  --hot-font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Helvetica Neue", Arial, sans-serif;
  --hot-cell-height: 22px;
  --hot-cell-line-height: 21px;
  --hot-cell-horizontal-padding: var(--hot-base-padding);
  --hot-cell-vertical-padding: 0;
  --hot-header-horizontal-padding: var(--hot-cell-horizontal-padding);
  --hot-header-vertical-padding: 2px;
  --hot-header-line-height: 1.1;
  --hot-resizer-width: 5px ;
}

.handsontable {
  --hot-col-width: 50px ;
}

/*
	Walkontable Foundation
*/
.handsontable {
  position: relative;
  /* It removes double right border from first column header when row headers are disabled */
  /*
  innerBorderTop - Property controlled by top overlay
  innerBorderBottom - Property controlled by bottom overlay
  */
}
.handsontable .hide {
  display: none;
}
.handsontable .relative {
  position: relative;
}
.handsontable .wtHider {
  width: 0;
}
.handsontable .wtSpreader {
  position: relative;
  /*must be 0, otherwise blank space appears in scroll demo after scrolling max to the right */
  width: 0;
  height: auto;
}
.handsontable table,
.handsontable tbody,
.handsontable thead,
.handsontable td,
.handsontable th,
.handsontable input,
.handsontable textarea,
.handsontable div {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
.handsontable input,
.handsontable textarea {
  min-height: initial;
}
.handsontable table.htCore {
  border-collapse: separate;
  /* it must be separate, otherwise there are offset miscalculations in WebKit: http://stackoverflow.com/questions/2655987/border-collapse-differences-in-ff-and-webkit */
  /* this actually only changes appearance of user selection - does not make text unselectable */
  /* -webkit-user-select: none;
      -khtml-user-select: none;
      -moz-user-select: none;
      -o-user-select: none;
      -ms-user-select: none;
      user-select: none; // no browser supports unprefixed version
  */
  border-spacing: 0;
  margin: 0;
  border-width: 0;
  table-layout: fixed;
  width: 0;
  outline-width: 0;
  cursor: default;
  /* reset bootstrap table style. for more info see: https://github.com/handsontable/handsontable/issues/224 */
  max-width: none;
  max-height: none;
}
.handsontable col {
  width: var(--hot-col-width);
}
.handsontable col.rowHeader {
  width: var(--hot-col-width);
}
.handsontable col.hidden {
  width: 0 !important;
}
.handsontable th, .handsontable td {
  border-top-width: 0;
  border-inline-start-width: 0;
  border-inline-end-width: var(--hot-border-vertical-size);
  border-bottom-width: var(--hot-border-horizontal-size);
  border-style: solid;
  height: var(--hot-cell-height);
  empty-cells: show;
  line-height: var(--hot-cell-line-height);
  padding: var(--hot-cell-vertical-padding) var(--hot-cell-horizontal-padding);
  /* top, bottom padding different than 0 is handled poorly by FF with HTML5 doctype */
  vertical-align: top;
  overflow: hidden;
  outline: none;
  outline-width: 0;
  white-space: pre-wrap;
}
.handsontable th {
  border-style: solid;
}
.handsontable th:last-child {
  /*Foundation framework fix*/
  border-inline-start-width: 0;
  border-inline-end-width: var(--hot-border-vertical-size);
  border-bottom-width: var(--hot-border-horizontal-size);
}
.handsontable th:first-child, .handsontable th:nth-child(2), .handsontable th:first-of-type {
  border-inline-start-width: var(--hot-border-vertical-size);
}
.handsontable .ht_clone_top th:nth-child(2) {
  border-inline-start-width: 0;
  border-inline-end-width: var(--hot-border-vertical-size);
}
.handsontable.htRowHeaders thead tr th:nth-child(2) {
  border-inline-start-width: var(--hot-border-vertical-size);
}
.handsontable tr:first-child th, .handsontable tr:first-child td {
  border-top-width: var(--hot-border-horizontal-size);
}
.handsontable .ht_master:not(.innerBorderInlineStart):not(.emptyColumns) ~ .handsontable tbody tr th, .handsontable .ht_master:not(.innerBorderInlineStart):not(.emptyColumns) ~ .handsontable:not(.ht_clone_top) thead tr th:first-child {
  border-inline-end-width: 0;
  border-inline-start-width: var(--hot-border-vertical-size);
}
.handsontable .ht_master:not(.innerBorderTop):not(.innerBorderBottom) thead tr:last-child th, .handsontable .ht_master:not(.innerBorderTop):not(.innerBorderBottom) ~ .handsontable thead tr:last-child th,
.handsontable .ht_master:not(.innerBorderTop):not(.innerBorderBottom) thead tr.lastChild th, .handsontable .ht_master:not(.innerBorderTop):not(.innerBorderBottom) ~ .handsontable thead tr.lastChild th {
  border-bottom-width: 0;
}
.handsontable th {
  text-align: center;
  font-weight: normal;
  white-space: nowrap;
}
.handsontable thead th {
  padding: 0;
}
.handsontable thead th.active {
  background-color: var(--hot-header-active-background-color);
}
.handsontable thead th .relative {
  padding: var(--hot-header-vertical-padding) var(--hot-header-horizontal-padding);
}
.handsontable span.colHeader {
  display: inline-block;
  line-height: var(--hot-header-line-height);
}
.handsontable tr.hidden {
  display: none;
}
.handsontable tr.hidden td,
.handsontable tr.hidden th {
  display: none;
}

/*
	Walkontable Selectio
*/
.handsontable .wtBorder {
  position: absolute;
  font-size: 0;
  /* A layer order of the selection types */
  /* fill handle */
}
.handsontable .wtBorder.hidden {
  display: none !important;
}
.handsontable .wtBorder.current {
  z-index: 10;
}
.handsontable .wtBorder.area {
  z-index: 8;
}
.handsontable .wtBorder.fill {
  z-index: 6;
}
.handsontable .wtBorder.corner {
  font-size: 0;
  cursor: crosshair;
}

/*
	Walkontable Elevation
*/
.ht_clone_master {
  z-index: 100;
}

.ht_clone_inline_start {
  z-index: 120;
}

.ht_clone_bottom {
  z-index: 130;
}

.ht_clone_bottom_inline_start_corner {
  z-index: 150;
}

.ht_clone_top {
  z-index: 160;
}

.ht_clone_top_inline_start_corner {
  z-index: 180;
}

/*
	Walkontable Foundation
*/
.ht_master,
.ht_clone_inline_start,
.ht_clone_top,
.ht_clone_bottom {
  overflow: hidden;
}
.ht_master .wtHolder,
.ht_clone_inline_start .wtHolder,
.ht_clone_top .wtHolder,
.ht_clone_bottom .wtHolder {
  overflow: hidden;
}

.ht_master .wtHolder {
  overflow: auto;
}

.handsontable .ht_master table.htCore > thead,
.handsontable .ht_master table.htCore > tbody > tr > th,
.handsontable .ht_clone_inline_start table.htCore > thead {
  visibility: hidden;
}

/*
	Handsontable Foundation
*/
.handsontable {
  touch-action: manipulation;
  font-weight: normal;
  font-family: var(--hot-font-family);
  font-size: var(--hot-base-font-size);
  color: var(--hot-foreground-color);
}
.handsontable .wtHider {
  background: var(--hot-background-color);
}
.handsontable a {
  color: var(--hot-link-color);
}
.handsontable .htAutoSize {
  visibility: hidden;
  left: -99000px;
  position: absolute;
  top: -99000px;
}
.handsontable td, .handsontable th {
  border-color: var(--hot-border-color);
  /*background-color: var(--hot-background-color); // moved into wtHider */
}
.handsontable td.invisibleSelection, .handsontable th.invisibleSelection {
  outline: none;
}
.handsontable td.invisibleSelection::selection, .handsontable th.invisibleSelection::selection {
  background: transparent;
}
.handsontable td.htInvalid {
  /*gives priority over td.area selection background*/
  background-color: var(--hot-invalid-background-color) !important;
}
.handsontable td.htNoWrap {
  white-space: nowrap;
}
.handsontable th {
  border-color: var(--hot-border-color);
  background-color: var(--hot-header-background-color);
  color: var(--hot-header-foreground-color);
  border-color: var(--hot-border-color);
}
.handsontable .htFocusCatcher {
  position: absolute;
  z-index: -1;
  opacity: 0;
  border: 0;
  margin: 0;
  padding: 0;
  width: 0;
  height: 0;
}

.handsontable tbody th.current,
.handsontable thead th.current {
  box-shadow: inset 0 0 0 2px #4b89ff;
}

.handsontable tbody th.ht__highlight,
.handsontable thead th.ht__highlight {
  background-color: #dcdcdc;
}

.handsontable tbody th.ht__active_highlight,
.handsontable thead th.ht__active_highlight {
  background-color: #8eb0e7;
  color: #000;
}

.handsontableInput {
  border: none;
  outline-width: 0;
  margin: 0;
  padding: 1px 5px 0 5px;
  font-family: inherit;
  line-height: 21px;
  font-size: inherit;
  box-shadow: 0 0 0 2px #5292F7 inset;
  resize: none;
  /*below are needed to overwrite stuff added by jQuery UI Bootstrap theme*/
  display: block;
  color: #000;
  border-radius: 0;
  background-color: #FFF;
  /*overwrite styles potentionally made by a framework*/
}

.handsontableInput:focus {
  outline: none;
}

.handsontableInputHolder {
  position: absolute;
  top: 0;
  left: 0;
}

.htSelectEditor {
  -webkit-appearance: menulist-button !important;
  position: absolute;
  width: auto;
}

.htSelectEditor:focus {
  outline: none;
}

/*
TextRenderer readOnly cell
*/
.handsontable .htDimmed {
  color: #777;
}

.handsontable .htSubmenu {
  position: relative;
}

.handsontable .htSubmenu :after {
  content: "▶";
  color: #777;
  position: absolute;
  right: 5px;
  font-size: 9px;
}

[dir=rtl].handsontable .htSubmenu :after {
  content: "";
}

[dir=rtl].handsontable .htSubmenu :before {
  content: "◀";
  color: #777;
  position: absolute;
  left: 5px;
  font-size: 9px;
}

/*
TextRenderer horizontal alignment
*/
.handsontable .htLeft {
  text-align: left;
}

.handsontable .htCenter {
  text-align: center;
}

.handsontable .htRight {
  text-align: right;
}

.handsontable .htJustify {
  text-align: justify;
}

/*
TextRenderer vertical alignment
*/
.handsontable .htTop {
  vertical-align: top;
}

.handsontable .htMiddle {
  vertical-align: middle;
}

.handsontable .htBottom {
  vertical-align: bottom;
}

/*
TextRenderer placeholder value
*/
.handsontable .htPlaceholder {
  color: #999;
}

/**
 * Handsontable listbox theme
 */
.handsontable.listbox {
  margin: 0;
}

.handsontable.listbox .ht_master table {
  border: 1px solid #ccc;
  border-collapse: separate;
  background: white;
}

.handsontable.listbox th,
.handsontable.listbox tr:first-child th,
.handsontable.listbox tr:last-child th,
.handsontable.listbox tr:first-child td,
.handsontable.listbox td {
  border-color: transparent !important;
}

.handsontable.listbox th,
.handsontable.listbox td {
  white-space: nowrap;
  text-overflow: ellipsis;
}

.handsontable.listbox td.htDimmed {
  cursor: default;
  color: inherit;
  font-style: inherit;
}

.handsontable.listbox .wtBorder {
  visibility: hidden;
}

.handsontable.listbox tr td.current,
.handsontable.listbox tr:hover td {
  background: #eee;
}

.ht_editor_hidden {
  z-index: -1;
}

.ht_editor_visible {
  z-index: 200;
}

.handsontable td.htSearchResult {
  background: #fcedd9;
  color: #583707;
}

.handsontable .collapsibleIndicator {
  position: absolute;
  top: 50%;
  transform: translate(0%, -50%);
  inset-inline-start: unset;
  inset-inline-end: 5px;
  border: 1px solid #A6A6A6;
  line-height: 8px;
  color: #222;
  border-radius: 10px;
  font-size: 10px;
  width: 10px;
  height: 10px;
  cursor: pointer;
  -webkit-box-shadow: 0 0 0 6px rgb(238, 238, 238);
  -moz-box-shadow: 0 0 0 6px rgb(238, 238, 238);
  box-shadow: 0 0 0 3px rgb(238, 238, 238);
  background: #eee;
  text-align: center;
}

/*
	Handsontable License info
*/
.hot-display-license-info {
  font-family: var(--hot-font-family);
  font-size: calc(var(--hot-base-font-size) - 3px);
  font-weight: normal;
  color: var(--hot-foreground-color);
  padding: var(--hot-base-padding) 0;
  text-align: left;
}
.hot-display-license-info a {
  color: var(--hot-link-color);
  font-size: 10px;
}

/* 
    Element / Manual Resizer
    row + column resizer
*/
.handsontable .manualColumnResizer {
  position: absolute;
  top: 0;
  cursor: col-resize;
  z-index: 210;
  width: var(--hot-resizer-width);
  height: calc(var(--hot-cell-height) + var(--hot-header-horizontal-padding));
}
.handsontable .manualColumnResizer:hover, .handsontable .manualColumnResizer.active {
  background-color: var(--hot-column-resizer-color);
}
.handsontable .manualRowResize:hover, .handsontable .manualRowResize.active {
  background-color: var(--hot-column-resizer-color);
}
.handsontable .manualColumnResizerGuide {
  position: absolute;
  inset-inline-end: unset;
  top: 0;
  background-color: var(--hot-column-resizer-color);
  display: none;
  width: 0;
  border-inline-end: var(--hot-border-vertical-size) dashed var(--hot-column-resizer-guide-color);
  border-inline-start: none;
  margin-inline-start: var(--hot-resizer-width);
  margin-inline-end: unset;
}
.handsontable .manualRowResizerGuide {
  position: absolute;
  left: 0;
  bottom: 0;
  background-color: var(--hot-column-resizer-color);
  display: none;
  height: 0;
  border-bottom: var(--hot-border-vertical-size) dashed var(--hot-column-resizer-guide-color);
  margin-top: var(--hot-resizer-width);
}
.handsontable .manualColumnResizerGuide.active,
.handsontable .manualRowResizerGuide.active {
  display: block;
  z-index: 209;
}

/* 
    Element / Column Sorting
*/
.handsontable {
  /* Arrow position */
}
.handsontable .columnSorting {
  position: relative;
}
.handsontable .columnSorting.sortAction:hover {
  text-decoration: underline;
  cursor: pointer;
}
.handsontable span.colHeader.columnSorting::before {
  /* Centering start */
  top: 50%;
  /* One extra pixel for purpose of proper positioning of sorting arrow, when `font-size` set to default */
  margin-top: -6px;
  /* Centering end */
  /* For purpose of continuous mouse over experience, when moving between the `span` and the `::before` elements */
  padding-inline-start: 8px;
  padding-inline-end: 0;
  position: absolute;
  inset-inline-end: -9px;
  inset-inline-start: unset;
  content: "";
  height: 10px;
  width: 5px;
  background-size: contain;
  background-repeat: no-repeat;
  background-position-x: right;
}
.handsontable span.colHeader.columnSorting.ascending::before {
  /* arrow up; 20 x 40 px, scaled to 5 x 10 px; base64 size: 0.3kB */
  background-image: url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAAoCAMAAADJ7yrpAAAAKlBMVEUAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAKE86IAAAADXRSTlMABBEmRGprlJW72e77tTkTKwAAAFNJREFUeAHtzjkSgCAUBNHPgsoy97+ulGXRqJE5L+xkxoYt2UdsLb5bqFINz+aLuuLn5rIu2RkO3fZpWENimNgiw6iBYRTPMLJjGFxQZ1hxxb/xBI1qC8k39CdKAAAAAElFTkSuQmCC");
}
.handsontable span.colHeader.columnSorting.descending::before {
  /* arrow down; 20 x 40 px, scaled to 5 x 10 px; base64 size: 0.3kB */
  background-image: url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAAoCAMAAADJ7yrpAAAAKlBMVEUAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAKE86IAAAADXRSTlMABBEmRGprlJW72e77tTkTKwAAAFJJREFUeAHtzjkSgCAQRNFmQYUZ7n9dKUvru0TmvPAn3br0QfgdZ5xx6x+rQn23GqTYnq1FDcnuzZIO2WmedVqIRVxgGKEyjNgYRjKGkZ1hFIZ3I70LyM0VtU8AAAAASUVORK5CYII=");
}

.htGhostTable .htCore span.colHeader.columnSorting:not(.indicatorDisabled)::before {
  content: "*";
  display: inline-block;
  position: relative;
  /* The multi-line header and header with longer text need more padding to not hide arrow,
  we make header wider in `GhostTable` to make some space for arrow which is positioned absolutely in the main table */
  padding-right: 20px;
}